// @flow

import React from 'react'
import {graphql, Link} from 'gatsby'
import styled from 'styled-components'
import {I18n} from 'aws-amplify'

import Layout from '../components/layout'
import SEO from '../components/seo'

type Props = {
  data: {
    markdownRemark: {|
      id: string,
      frontmatter: {|
        title: string,
      |},
      excerpt: any,
      html: string,
    |},
    site: {
      siteMetadata: {|
        title: string,
      |},
    },
  },
  location: Location,
}

const MyArticle = styled.article`
  max-width: ${props => props.theme.spacing(84)}px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: ${props => props.theme.spacing(10)}px;
  font-family: 'Nanum Gothic', 'Courier New', Courier, monospace, Georgia, Cambria,
    'Times New Roman', Times, serif;
  font-size: 15px;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding-top: ${props => props.theme.spacing(10)}px;
  padding-bottom: ${props => props.theme.spacing(10)}px;
`

const MyLink = styled(Link)`
  color: inherit;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
`

const Apps = (props: Props) => {
  const {data, location} = props

  const {
    site: {
      siteMetadata: {title: siteTitle},
    },
  } = data

  return (
    <Layout location={location} title={siteTitle} posts={[]}>
      <SEO title={I18n.get('apps')} description={I18n.get('apps')} />
      <MyArticle>
        <MyLink to="/apps/eye-training">{I18n.get('eyeTraining')}</MyLink>
        <MyLink to="/apps/kegel-training">{I18n.get('kegelTraining')}</MyLink>
      </MyArticle>
    </Layout>
  )
}

export default Apps

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
